@import url(https://fonts.googleapis.com/css?family=Spartan&display=swap);
html{box-sizing:border-box;font-size:16px}*,*:before,*:after{box-sizing:inherit}body,h1,h2,h3,h4,h5,h6,p,ol,ul{margin:0;padding:0}ol,ul{list-style:none}img{max-width:100%;height:auto}input[type="file"]{opacity:0;height:0;padding:0;margin:0}body,input,textarea,button{font-family:"Spartan", Arial, sans-serif;color:#424242}.custom-file-upload{height:2.5em;margin:1em 0.5em 0.5em;padding-top:0.6em;background-color:#114b5f;color:white;border:2px solid #456990;border-radius:4px;display:inline-block;cursor:pointer;-webkit-transition:all 0.1s ease-out;transition:all 0.1s ease-out}.custom-file-upload:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}.book-form{padding:1em;width:40%;min-width:20em;margin:auto;font-weight:bold}.book-form__element{margin:0.5em auto;padding:0.5em;width:50%;min-width:25vh;display:flex;flex-direction:column;justify-content:center}.book-form__label{margin:1em}.book-form__input{font-size:1.1em;height:2.5em;margin:0.5em;padding:0.5em;font-weight:bold;border-radius:4px;border:2px solid #456990}.book-form__input:focus,.book-form__input :active{outline:3px solid #114b5f}.book-form__input:valid{background:#e4fde1}.book-form__input:invalid{background:#ffaab1}.book-form__control{width:50%;min-width:15em;margin:0.5em auto;padding:1em;display:flex;justify-content:space-around}.book-form--hidden{display:none}.book-form:not(.book-form--hidden){-webkit-animation:drop-down 0.7s cubic-bezier(0.18, 0.89, 0.32, 1.28);animation:drop-down 0.7s cubic-bezier(0.18, 0.89, 0.32, 1.28)}.book__card{min-height:700px;min-width:250px;border:1px solid #456990;border-radius:4px;padding:1em;margin:1em 1em 2em;flex-basis:25em;text-align:center;background:#e9f9ff;box-shadow:6px 6px 10px #8d8d8d;-webkit-transition:all 0.1s ease-out;transition:all 0.1s ease-out}.book__card:hover{-webkit-transform:scale(1.02);transform:scale(1.02)}.book__card__title{font-size:1.7em;color:#114b5f;margin:1em}.book__card__author{font-size:1.4em;font-style:italic;color:#456990;margin:1em}.book__card__cover{width:50%;height:40%;object-fit:contain;margin-bottom:1em}.book__card__pages{color:#114b5f;width:80%;margin:auto;padding:1em;border-bottom:1px solid #114b5f}.book__card__status{color:#456990;font-weight:bold;padding:1em}.book__card__controls{display:flex;width:80%;margin:1em auto auto;justify-content:space-around}.button{text-transform:uppercase;font-weight:bold;padding:0.8em 0.6em 0.6em;margin:auto;border:1px solid black;background-color:transparent;cursor:pointer;-webkit-transition:all 0.1s ease-out;transition:all 0.1s ease-out;height:3em;min-width:5em;transition:all 0.1s ease-out}.button:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}.button:focus,.button :active{outline:2px solid #114b5f}.button__form-toggle{width:10vw;min-width:8em;height:5vh;margin:5em auto}.button--primary{background-color:#456990;color:white;border:1px solid #114b5f}.button--secondary{background-color:white;color:#456990;border:1px solid #456990}.button--hidden{display:none}.file-preview{color:#8f8f8f}.file-preview__text{padding:1em 0}.file-preview__cover{width:10em;height:15em;object-fit:contain}.header{text-align:center;background-color:#114b5f}.header-title{padding:1em;color:white}.input-container{min-height:20vh;margin-bottom:1em;height:auto;text-align:center}.main-section{width:80%;margin:auto;display:flex;justify-content:space-around;flex-wrap:wrap}@-webkit-keyframes drop-down{0%{display:block;opacity:0;-webkit-transform:translateY(-50%);transform:translateY(-50%)}20%{opacity:0.1}40%{opacity:0.3}60%{opacity:0.5}80%{opacity:0.7}100%{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}}@keyframes drop-down{0%{display:block;opacity:0;-webkit-transform:translateY(-50%);transform:translateY(-50%)}20%{opacity:0.1}40%{opacity:0.3}60%{opacity:0.5}80%{opacity:0.7}100%{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}}@media screen and (max-width: 500px){.book__card__controls{flex-direction:column;margin:auto}.book__card__controls .button{margin:1em}.main-section{width:100%}.book__card__cover{height:30%;width:40%}}

